/* ====================================
   COMMON SECTION STYLES
   ==================================== */
.section {
  min-height: 100vh;
  padding: 5rem 4rem;
}

.section-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 3rem;
}

.section-icon {
  font-size: 1.875rem;
}

.section-heading {
  font-size: 1.875rem;
  font-weight: bold;
}

/* ====================================
   INTRO SECTION
   ==================================== */
.intro-section {
  display: flex;
  align-items: center;
}

.intro-content {
  max-width: 64rem;
}

.intro-badge {
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.2);
  border-radius: 9999px;
  font-size: 0.875rem;
  color: #d8b4fe;
  margin-bottom: 2rem;
}

.intro-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 2rem;
  line-height: 1.2;
}

.intro-title-gradient {
  display: block;
  background: linear-gradient(to right, #c084fc, #e879f9, #f9a8d4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.intro-text {
  font-size: 1.25rem;
  color: #9ca3af;
  margin-bottom: 1.5rem;
  line-height: 1.75;
  max-width: 48rem;
}

.intro-bio {
  font-size: 1.125rem;
  color: #6b7280;
  margin-bottom: 2.5rem;
  line-height: 1.75;
  max-width: 48rem;
}

.intro-buttons {
  display: flex;
  gap: 1rem;
}

.btn-primary {
  padding: 1rem 2rem;
  background: linear-gradient(to right, #a855f7, #d946ef);
  border: none;
  border-radius: 0.75rem;
  font-weight: 500;
  color: white;
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  text-decoration: none;
}

.btn-primary:hover {
  box-shadow: 0 20px 25px -5px rgba(139, 92, 246, 0.5);
  transform: scale(1.05);
}

.btn-secondary {
  padding: 1rem 2rem;
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.75rem;
  font-weight: 500;
  color: white;
  cursor: pointer;
  transition: all 0.3s;
  text-decoration: none;
  display: inline-block;
}

.btn-secondary:hover {
  background-color: rgba(255, 255, 255, 0.1);
  transform: scale(1.05);
}

/* ====================================
   PROJECTS SECTION
   ==================================== */
.projects-grid {
  display: grid;
  gap: 2rem;
  max-width: 80rem;
}

.project-card {
  position: relative;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 1rem;
  transition: all 0.5s;
  cursor: pointer;
  overflow: hidden;
  display: flex;
  gap: 1.5rem;
  align-items: flex-start;
}

.project-card:hover {
  border-color: rgba(139, 92, 246, 0.3);
  transform: scale(1.01);
}

.project-card::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(to bottom right, transparent, transparent, rgba(139, 92, 246, 0.05));
  opacity: 0;
  transition: opacity 0.5s;
}

.project-card:hover::before {
  opacity: 1;
}

/* Video Container - Piccolo a sinistra */
.project-video-container {
  position: relative;
  width: 240px;
  min-width: 240px;
  height: 135px;
  border-radius: 0.75rem;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
  flex-shrink: 0;
}

.project-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 0.75rem;
  pointer-events: none;
}

.project-card:hover .project-video-container {
  box-shadow: 0 10px 30px rgba(139, 92, 246, 0.3);
}

/* Contenuto progetto */
.project-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin-bottom: 1.5rem;
  position: relative;
  z-index: 1;
}

.project-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  transition: color 0.3s;
}

.project-card:hover .project-title {
  color: #a855f7;
}

.project-type {
  font-size: 0.875rem;
  color: #9ca3af;
}

.project-icon {
  color: #6b7280;
  transition: all 0.3s;
}

.project-icon-link {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  cursor: pointer;
}

.project-card:hover .project-icon,
.project-icon-link:hover .project-icon {
  color: #a855f7;
  transform: rotate(45deg);
}

.project-description {
  color: #d1d5db;
  margin-bottom: 1.5rem;
  line-height: 1.75;
  position: relative;
  z-index: 1;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  position: relative;
  z-index: 1;
}

.project-tag {
  padding: 0.25rem 0.75rem;
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.5rem;
  font-size: 0.75rem;
  font-weight: 500;
}

/* ====================================
   EXPERIENCE SECTION
   ==================================== */
.experience-timeline {
  max-width: 64rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.experience-item {
  position: relative;
  padding-left: 2rem;
  border-left: 2px solid rgba(255, 255, 255, 0.1);
  transition: border-color 0.3s;
}

.experience-item:hover {
  border-color: rgba(139, 92, 246, 0.5);
}

.experience-dot {
  position: absolute;
  left: -0.5rem;
  top: 0;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #6b7280;
}

.experience-dot.current {
  background-color: #a855f7;
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

.experience-header {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin-bottom: 0.75rem;
}

.experience-title {
  font-size: 1.25rem;
  font-weight: bold;
  transition: color 0.3s;
}

.experience-item:hover .experience-title {
  color: #a855f7;
}

.experience-company {
  color: #a855f7;
  font-weight: 500;
}

.current-badge {
  padding: 0.25rem 0.75rem;
  background-color: rgba(139, 92, 246, 0.2);
  border: 1px solid rgba(139, 92, 246, 0.3);
  border-radius: 9999px;
  font-size: 0.75rem;
  color: #d8b4fe;
}

.experience-meta {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 0.875rem;
  color: #9ca3af;
  margin-bottom: 0.75rem;
}

.experience-description {
  color: #d1d5db;
  line-height: 1.75;
}

/* ====================================
   EDUCATION SECTION
   ==================================== */
.education-grid {
  max-width: 64rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.education-card {
  padding: 2rem;
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 1rem;
  transition: all 0.3s;
}

.education-card:hover {
  border-color: rgba(139, 92, 246, 0.3);
  background-color: rgba(255, 255, 255, 0.1);
}

.education-title {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  transition: color 0.3s;
}

.education-card:hover .education-title {
  color: #a855f7;
}

.education-school {
  color: #a855f7;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.education-meta {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-size: 0.875rem;
  color: #9ca3af;
  margin-bottom: 1rem;
}

.education-description {
  color: #d1d5db;
  line-height: 1.75;
}

/* ====================================
   CERTIFICATIONS SECTION
   ==================================== */
.certifications-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  max-width: 80rem;
}

.certification-card {
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.75rem;
  transition: all 0.3s;
  cursor: pointer;
  overflow: hidden;
}

.certification-card:hover {
  border-color: rgba(139, 92, 246, 0.3);
  background-color: rgba(255, 255, 255, 0.1);
  transform: scale(1.03);
}

/* Immagine preview certificato */
.certification-image-preview {
  position: relative;
  width: 100%;
  height: 200px;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.3);
}

.certification-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s;
}

.certification-card:hover .certification-img {
  transform: scale(1.05);
}

.certification-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s;
}

.certification-card:hover .certification-overlay {
  opacity: 1;
}

.certification-view-text {
  color: white;
  font-size: 1rem;
  font-weight: 600;
}

/* Info certificato */
.certification-info {
  padding: 1.5rem;
}

.certification-header {
  display: flex;
  justify-content: flex-end;
  align-items: start;
  margin-bottom: 0.75rem;
}

.certification-year {
  font-size: 0.75rem;
  color: #9ca3af;
  padding: 0.25rem 0.75rem;
  background: rgba(139, 92, 246, 0.2);
  border-radius: 9999px;
}

.certification-name {
  font-weight: bold;
  margin-bottom: 0.5rem;
  transition: color 0.3s;
  font-size: 1rem;
}

.certification-card:hover .certification-name {
  color: #a855f7;
}

.certification-issuer {
  font-size: 0.875rem;
  color: #9ca3af;
}

/* Modal per certificato ingrandito */
.certification-modal {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  padding: 2rem;
  animation: fadeIn 0.3s ease-out;
}

.certification-modal-content {
  position: relative;
  max-width: 90vw;
  max-height: 90vh;
  animation: slideUp 0.3s ease-out;
}

.certification-modal-close {
  position: absolute;
  top: -3rem;
  right: 0;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  font-size: 1.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
}

.certification-modal-close:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: rotate(90deg);
}

.certification-modal-img {
  max-width: 100%;
  max-height: 70vh;
  border-radius: 1rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
}

.certification-modal-info {
  text-align: center;
  margin-top: 1.5rem;
  color: white;
}

.certification-modal-info h3 {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.certification-modal-info p {
  color: #9ca3af;
}

/* ====================================
   CONTACT SECTION
   ==================================== */
.contact-section {
  padding: 2rem 1.5rem;
}

.contact-content {
  max-width: 64rem;
  margin: 0 auto;
  text-align: center;
}

/* Immagine con crediti */
.contact-image-wrapper {
  position: relative;
  max-width: 400px;
  margin: 0 auto 3rem;
  border-radius: 1rem;
  overflow: hidden;
}

.contact-image {
  width: 100%;
  height: auto;
  border-radius: 1rem;
  transition: transform 0.3s;
}

.contact-image-wrapper:hover .contact-image {
  transform: scale(1.05);
}

.contact-image-credits {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.9), transparent);
  opacity: 0;
  transition: opacity 0.3s;
}

.contact-image-wrapper:hover .contact-image-credits {
  opacity: 1;
}

.contact-image-credits p {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.9);
  margin: 0;
}

.contact-image-credits a {
  color: #a855f7;
  text-decoration: none;
  font-weight: 600;
}

.contact-image-credits a:hover {
  text-decoration: underline;
}

.contact-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
}

.contact-text {
  font-size: 1.07rem;
  color: #9ca3af;
  margin-bottom: 2.5rem;
}

.contact-button {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1.25rem 2.5rem;
  background: linear-gradient(to right, #a855f7, #d946ef);
  border: none;
  border-radius: 0.75rem;
  font-weight: bold;
  font-size: 1.120rem;
  color: white;
  cursor: pointer;
  text-decoration: none;
  transition: all 0.3s;
}

.contact-button:hover {
  box-shadow: 0 25px 50px -12px rgba(139, 92, 246, 0.5);
  transform: scale(1.05);
}

/* ====================================
   RESPONSIVE
   ==================================== */
@media (max-width: 1024px) {
  .section {
    padding: 4rem 2rem;
  }

  .intro-title {
    font-size: 2.5rem;
  }
}

@media (max-width: 768px) {
  .section {
    padding: 3rem 1.5rem;
  }

  .intro-title {
    font-size: 2rem;
  }

  .section-heading {
    font-size: 1.5rem;
  }

  .certifications-grid {
    grid-template-columns: 1fr;
  }

  .intro-buttons {
    flex-direction: column;
  }

  .contact-title {
    font-size: 2rem;
  }

  .contact-image-wrapper {
    max-width: 300px;
  }

  /* Projects responsive - Video sopra su mobile */
  .project-card {
    flex-direction: column;
    padding: 1.5rem;
  }

  .project-video-container {
    width: 100%;
    height: auto;
    padding-bottom: 56.25%;
  }

  .project-video {
    position: absolute;
  }

  .project-content {
    gap: 0.75rem;
  }

  /* Education cards più compatte */
  .education-card {
    padding: 1.5rem;
  }

  /* Contact section più compatta */
  .contact-section {
    padding: 4rem 1.5rem;
  }

  .contact-button {
    width: 100%;
    justify-content: center;
  }
}